"use client"

import React, { useEffect, useState } from 'react'
import { Bell, Check, X } from 'lucide-react'
import apiFetch from '../lib/api'
import { formatDistanceToNow } from 'date-fns'
import ptBR from 'date-fns/locale/pt-BR'

type Notificacao = {
  id: string
  titulo: string
  corpo?: string | null
  lida: boolean
  criadaEm: string
}

export function NotificationCenter() {
  const [open, setOpen] = useState(false)
  const [notificacoes, setNotificacoes] = useState<Notificacao[]>([])
  const [loading, setLoading] = useState(false)

  const fetchNotificacoes = async () => {
    setLoading(true)
    try {
      // Se não houver token armazenado, não tenta buscar (rota protegida)
      if (typeof window !== 'undefined') {
        const t = localStorage.getItem('token')
        if (!t) {
          setNotificacoes([])
          setLoading(false)
          return
        }
      }

      const res: any = await apiFetch('/api/notificacoes')
      setNotificacoes(res.data || [])
    } catch (err) {
      // Se for erro de autenticação, não poluir o console (usuário pode não estar logado)
      const status = (err as any)?.status
      const msg = (err as any)?.message || ''
      if (status === 401 || /token/i.test(msg) || /acesso obrigatório/i.test(msg)) {
        // Silencioso: limpar lista e parar
        setNotificacoes([])
      } else {
        console.error('Erro ao buscar notificações', err)
      }
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchNotificacoes()
    // Poll every 30s for new notifications while component mounted
    const iv = setInterval(fetchNotificacoes, 30000)
    return () => clearInterval(iv)
  }, [])

  const unreadCount = notificacoes.filter(n => !n.lida).length

  const marcarLidas = async (ids: string[]) => {
    try {
      await apiFetch('/api/notificacoes/marcar-lidas', {
        method: 'POST',
        body: JSON.stringify({ ids })
      })
      // otimista
      setNotificacoes(prev => prev.map(p => ids.includes(p.id) ? { ...p, lida: true } : p))
    } catch (err) {
      console.error('Erro ao marcar lidas', err)
    }
  }

  return (
    <div className="relative">
      <button aria-label="Notificações" onClick={() => setOpen(!open)} className="relative p-2 rounded-md hover:bg-slate-100">
        <Bell className="w-5 h-5" />
        {unreadCount > 0 && (
          <span className="absolute -top-0.5 -right-0.5 bg-red-600 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center">{unreadCount}</span>
        )}
      </button>

      {open && (
        <div className="absolute right-0 mt-2 w-80 bg-white border rounded-md shadow-lg z-50">
          <div className="px-3 py-2 border-b flex items-center justify-between">
            <strong>Notificações</strong>
            <div className="flex items-center gap-2">
              <button onClick={() => marcarLidas(notificacoes.filter(n => !n.lida).map(n => n.id))} className="text-sm text-slate-600 hover:underline">Marcar todas</button>
              <button onClick={() => setOpen(false)} className="p-1 rounded hover:bg-slate-100"><X className="w-4 h-4"/></button>
            </div>
          </div>

          <div className="max-h-72 overflow-auto">
            {loading && <div className="p-3">Carregando...</div>}
            {!loading && notificacoes.length === 0 && <div className="p-3 text-sm text-slate-600">Sem notificações</div>}

            {notificacoes.map(n => (
              <div key={n.id} className={`px-3 py-2 border-b hover:bg-slate-50 flex items-start gap-2 ${n.lida ? 'opacity-60' : ''}`}>
                <div className="flex-1">
                  <div className="flex items-center justify-between gap-2">
                    <div className="text-sm font-medium">{n.titulo}</div>
                    <div className="text-xs text-slate-500">{formatDistanceToNow(new Date(n.criadaEm), { addSuffix: true, locale: ptBR })}</div>
                  </div>
                  {n.corpo && <div className="text-sm text-slate-700 mt-1">{n.corpo}</div>}
                </div>
                <div className="flex flex-col gap-1 pl-2">
                  {!n.lida ? (
                    <button title="Marcar como lida" onClick={() => marcarLidas([n.id])} className="p-1 rounded bg-green-50 text-green-600 hover:bg-green-100"><Check className="w-4 h-4"/></button>
                  ) : (
                    <span className="text-xs text-slate-400">Lida</span>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  )
}

export default NotificationCenter
